document.addEventListener('DOMContentLoaded', function(){
    iniciarApp();
})
function iniciarApp(){
    stories();
    likes();
}

function stories(){
    const stories = document.querySelector('.stories-imagenes');
    for(let i=1; i<=6; i++){
        const imagen = document.createElement('picture');
        const divDescript = document.createElement('DIV');
        const agregarStorie = document.createElement('DIV');
        imagen.innerHTML=`
        <source type="image/webp" srcset="build/img/Stories/${i}.webp">
        <img loading="lazy" src="build/img/Stories/${i}.png">
        `;
        divDescript.innerHTML = `<p>Edgar RV</p>`;
        agregarStorie.innerHTML=`<p>+</p>`
        divDescript.classList.add('divDescript');
        imagen.classList.add('historiaPic');
        agregarStorie.classList.add('agregarStorie');
        stories.appendChild(imagen);
        stories.appendChild(divDescript);
        stories.appendChild(agregarStorie)
    }
}
function likes(){
    const btnLike = document.querySelector('.like');
    const btnLike2 = document.querySelector('.like2');
    const btnLike3 = document.querySelector('.like3');
    
    btnLike.addEventListener('click', disteLike);
    btnLike.addEventListener('dblclick', disLike);
    function disteLike(){btnLike.classList.add('disteLike');}
    function disLike(){btnLike.classList.remove('disteLike');}

    btnLike2.addEventListener('click', disteLike2);
    btnLike2.addEventListener('dblclick', disLike2);
    function disteLike2(){btnLike2.classList.add('disteLike');}
    function disLike2(){btnLike2.classList.remove('disteLike');}

    btnLike3.addEventListener('click', disteLike3);
    btnLike3.addEventListener('dblclick', disLike3);
    function disteLike3(){btnLike3.classList.add('disteLike');}
    function disLike3(){btnLike3.classList.remove('disteLike');}
}